<template>
  <section v-html="content" class="md-container">
  </section>
</template>

<script>
import '../assets/highlight.js';
import '../assets/marked.js';

export default {
  name: "MD",
  props: {
    allIndex: {
      type: Number,
      default: 0
    },
    list: {
      type: Object,
      default: {}
    }
  },
  computed:{
    content(){
      if(this.list.all[this.allIndex].suffix == 'md'){
        return marked(this.list.all[this.allIndex].cacheContent);
      }else{
        return this.list.all[this.allIndex].cacheContent.replace(/\n/g,'<br />');
      }
    }
  },
  created(){
    marked.setOptions({
      highlight: function (code) {
        return hljs.highlightAuto(code).value;
      }
    });
  }
};
</script>

<style scoped lang="less">
</style>
