<template>
  <section class="img-container">
      <div class="title">
      {{list.all[allIndex].fileName}}

      </div>
    <img class="img" :src="contentSrc" :alt="list.all[allIndex].fileName" preview="0">
  </section>
</template>

<script>
export default {
  name: "IMG",
  name: "MD",
  props: {
    allIndex: {
      type: Number,
      default: 0
    },
    list: {
      type: Object,
      default: {}
    }
  },
  computed:{
    contentSrc(){
      return this.list.all[this.allIndex].cacheContent;
    }
  }
};
</script>

<style scoped lang="less">
.img{
  max-height: 100%;
  max-width: 100%;
}
.title{
  font-size: .25rem;
}
</style>
