<template>
  <div class="home wh100">
    <header class="header-footer flexcenter">
      
      <div class="switch" @click="handleNavSwitch">
        <i :class="['el-icon-arrow-right',navFlag ? 'deg180': '']"></i>
      </div>
    </header>
    <main class="main">
      <article v-show="showContentFlag == 'list'" class="list wh100">
        <section class="section" v-for="(n,index) in showList" :key="index">
          <div class="wrap">
            <div :class="['index',n.cacheFlag ? 'index-active': '',loadingFlag ? 'index-loading': '']" v-tap="handleTapIcon" v-longtap="handleLongtapIcon" :title="n.fileName">{{index+1}}</div>
            <div :class="['title',n.cacheFlag ? 'title-active': '']" v-tap="handleTapTitle" v-longtap="handleLongtapTitle" :title="n.fileName">{{n.fileName}}</div>
          </div>
        </section>
      </article>
      <m-d v-show="showContentFlag == 'md'" class="md wh100" :all-index="mdIndex" :list="list"></m-d>
      <i-m-g v-show="showContentFlag == 'img'" class="img wh100" :all-index="imgIndex" :list="list"></i-m-g>
      <node-nav 
        class="node-nav" 
        :list="list" 
        :class="navFlag ? 'node-nav-active' : ''"
        @selectnav="handleChangeNav"
      ></node-nav>
    </main>
    <footer class="header-footer flexcenter" v-tap="handleTapFooter">
      <span></span>
      <div class="subheading" v-show="showContentFlag != 'list'"></div>
    </footer>
    <div class="shade flexcenter" @click="shadeFlag = false" v-show="shadeFlag">
      
    </div>
  </div>
</template>

<script>
import IMG from './IMG.vue';
import MD from './MD.vue';
import NodeNav from './NodeNav.vue';


export default {
  name: "Home",
  data(){
    return {
      loadingFlag:false,
      showContentFlag: 'list',
      shadeFlag:false,
      navFlag:false,
      imgIndex: 0,
      mdIndex: 0,
      list:{
        all:[],
        typeName:[]
      },
      baseUrl:'',
      showList:[],
      myJSON:{},
      allowCacheType:['md','txt','png', 'webp', 'jpg', 'gif', 'bmp', 'jpeg'],
    }
  },
  methods:{
    handleTapFooter(){
      this.showContentFlag = 'list';
    },
    handleNavSwitch(){
      this.navFlag = !this.navFlag;
    },
    handleTapIcon(event){
      const fileName = event.target.title;
      const allIndex = this.byFileNameGetListAllIndex(fileName);
      if(this.list.all[allIndex].cacheFlag){// 
        this.showContentFlag = 'md';
        this.mdIndex = allIndex;
      }else{// 
        this.getFileContent(allIndex);
      }
    },
    handleLongtapIcon(event){
      const fileName = event.target.title;
      const allIndex = this.byFileNameGetListAllIndex(fileName);
      if(this.list.all[allIndex].cacheFlag){// 
        this.removeClearCache(allIndex);
      }else{// 

      }
    },
    handleTapTitle(event,callee){ //
      const fileName = event.target.title;
      const allIndex = this.byFileNameGetListAllIndex(fileName);
      if(this.list.all[allIndex].cacheFlag){// 
        if(this.list.all[allIndex].suffix == 'md' || this.list.all[allIndex].suffix == 'txt'){
          this.showContentFlag = 'md';
          this.mdIndex = allIndex;
        }else{
          this.showContentFlag = 'img';
          this.imgIndex = allIndex;
        }
      }else{// 
        window.open(this.baseUrl + fileName); 
      }
    },
    handleLongtapTitle(event){
      console.log('');
    },
    test(){
     console.log('longan');
    },
    handleIndex(){
      console.log('tap');
    },
    handleTitle(){

    },
    handleChangeNav(typeName){
      this.showList = this.list[typeName];
      this.navFlag = false;
    },
    getJSON(){//json
      // return nodelist;
      this.myJSON= Object.assign({},node_list);
    },
    buildData(){//
      this.baseUrl = this.myJSON['baseUrl'];
      for (let n in this.myJSON){
        if(n !== 'baseUrl'){
          // 
          this.list.typeName.push(n);
          this.myJSON[n].forEach(item=>{
            const localItem = this.getLocalObject(item);
            // 
            this.list[n] = this.list[n] || [];
            this.list[n].push({fileName:item});

            // 
            this.list.all = this.list.all.concat({
              fileName: item,
              suffix:this.$utils.getSuffix(item),
              cacheFlag:localItem.content ? true : false,
              cacheContent: localItem.content ? localItem.content : '',
              collect: localItem.collect ? localItem.collect : [],
              type:n
            });
          });
        }
      }
    },
    byFileNameGetListAllIndex(fileName){
      let result = 0; //
      this.list.all.every((item,index)=>{
        if( item.fileName == fileName ){
          result = index;
          return false;
        }
        return true;
      });
      return result;
    },
    removeCollect(listAllIndex,collectIndex){//allindexlocalStorage
      const fileName = this.list.all[listAllIndex].fileName;
      const temp = this.getLocalObject(fileName);
      //
      if(!temp) return;
      if(!temp.collect.length) return;
      temp.collect.splice(collectIndex,1);
      localStorage[fileName] = JSON.stringify(temp);
      //
      this.list.all[listAllIndex].colloect.splice(collectIndex,1);
    },
    removeClearCache(listAllIndex){//
      localStorage.removeItem(this.list.all[listAllIndex].fileName);
      this.list.all[listAllIndex].cacheContent = '';
      this.list.all[listAllIndex].cacheFlag = false;
    },
    getFileContent(listAllIndex){//
      if(!this.checkCanCache(listAllIndex)) return alert('');
      this.loadingFlag = true;
      const url = this.baseUrl + this.list.all[listAllIndex].type + '/'+this.list.all[listAllIndex].fileName;
      console.log(url);
      new Promise((ok,no)=>{
        if(this.list.all[listAllIndex].suffix == 'md' || this.list.all[listAllIndex].suffix == 'txt'){
          this.$utils.ajax('GET',url).then(res=>{
            ok(res);
          }).catch(res=>{
            no(res);
          });
        }else{
          this.$utils.imgToBese64(url).then(res=>{
            ok();
          }).catch(res=>{
            no(res);
          });
        }
      }).then(res=>{
        this.list.all[listAllIndex].cacheContent = res;
        this.list.all[listAllIndex].cacheFlag = true;
        this.toLocalSave(listAllIndex);
        this.loadingFlag = false;
      }).catch(res=>{
        this.loadingFlag = false;
      });

    },
    toLocalSave(listAllIndex){//allindexlocalStorage
      const temp = this.list.all[listAllIndex];
      localStorage[temp.fileName] = JSON.stringify({
        content: temp.cacheContent,
        collect: temp.collect
      })
    },
    checkCanCache(listAllIndex){//
      return this.allowCacheType.includes(this.list.all[listAllIndex].suffix);
    },
    getLocalObject(msg){
      const fileName = (typeof msg == 'number') ? this.list.all[msg].fileName : msg;
      const temp = localStorage[fileName] || '{}';
      return JSON.parse(temp);
    }
    // getsuffix(input)
  },
  created(){
    this.getJSON();
    this.myJSON = res;
    this.buildData();
    this.showList = this.list.all;
  },
  components:{
    IMG,MD,NodeNav
  }
};
</script>

<style scoped lang="less">
// #cc95c0#dbd4b4#7aa1d2
@green: green;
@keyframes loading {
  0%{
    background:  #cc95c0;
  }
  33%{
    background:  #dbd4b4;
  }
  67%{
    background:  #7aa1d2;
  }
  100%{
    background:  #cc95c0;
  }
}

.home{
  .header-footer{
    width: 100%;
    height: 50px;
    background: #000;
    color:#fff;
    position:relative;
    user-select: none;
    text-align: center;
    flex-direction: column;
    .switch{
      position: absolute;
      top:10px;
      left:.2rem;
      width:30px;
      height:30px;
      line-height: 30px;
      cursor: pointer;
      border-radius: 40%;
      box-sizing: border-box;
      &:hover{
        border:1px solid #666;
      }
      i{
        transition:all .4s;
      }
    }
    .deg180{
      transform: rotate(180deg);
    }
    .subheading{
      font-size: 8px;;
    }
  }
  .main{
    width: 100%;
    height: calc( 100% - 100px );
    position: relative;
    .list{
      display:flex;
      flex-direction: column;
      overflow: auto;
      background-color: rgb(241, 241, 241);
      padding-bottom: 1rem;
      box-sizing: border-box;
      user-select: none;
    }
    .node-nav{
      position: absolute;
      top:0;
      left:-80%;
      transition: all .4s ease-in-out;
    }
    .node-nav-active{
      left: 0;
    }
    .md{
      overflow-y: auto;
      overflow-x: hidden;
      background-color: #fafafa;
    }
    .img{
      overflow: hidden;
      display:flex;
      justify-content: center;
      align-items: center;
      background-color: #fafafa;
      flex-direction: column;
      // line-height: 30rem;
    }
  }
}
.section{
  width:100%;
  height:.50rem;
  box-sizing: border-box;
  padding: 0 .2rem;
  .wrap{
    width:auto;
    height:100%;
    display:flex;
    align-items: center;
    flex:0 0 auto;
    flex-wrap: nowrap;
    border-bottom: 1px solid #666;
    .index{
      width:.4rem;
      height:.4rem;
      background-color: rgb(123, 156, 152);
      line-height: .4rem;
      text-align: center;
      font-size: .14rem;
      margin-right: .1rem;
      border-radius: 40%;
      color:#fff;
      cursor: pointer;
    }
    .index-loading{
      animation: loading 3s infinite;
    }
    .index-active{
      background-color: @green;
    }
    .title{
      flex:1;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      height:.3rem;
      line-height: .3rem;
      cursor: pointer;
    }
    .title-active{
      color: @green;
    }
  }
  &:last-child .wrap{
    border-bottom: none;
  }
}
.shade{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.842);
  color:#fff;
  font-size: 30px;
  box-sizing: border-box;
  padding: 20px;
  text-align: center;
}

</style>