<template>
  <nav class="nav">
      <el-menu
        default-active="all"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b"
        @select="handleChangeNav"
        class="el-menu"
      >
        <el-menu-item index="all">
          <i class="el-icon-menu"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-location"></i>
            <span></span>
          </template>
          <el-menu-item-group v-for="(n,index) in list.typeName" :key="index">
            <el-menu-item :index="n">{{n}}</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
        <el-menu-item index="3" disabled>
          <i class="el-icon-setting"></i>
          <span slot="title"></span>
        </el-menu-item>
      </el-menu>
  </nav>
</template>

<script>
export default {
  name: "NodeNav",
  props: {
    list: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {

    };
  },
  methods: {
    handleChangeNav(typeName) {
      this.$emit('selectnav',typeName);
    }
  }
};
</script>

<style scoped lang="less">
.nav{
  width: 80%;
  height:100%;
  background-color: #545c64;
}
</style>
